@Composable
fun VerticalNavBar(modifier: Modifier = Modifier) {
    Column(
        modifier = modifier
            .fillMaxHeight()
            .borderRight(
                width = 1.px,
                style = LineStyle.Solid,
                color = colors(
                    ifDark = { Color.whitesmoke },
                    ifLight = { Color.black }
                ))
            .margin(right = 24.px)
    ) {
        val linkVariant = UndecoratedLinkVariant then UncoloredLinkVariant
        Column(modifier = Modifier.minWidth(200.px).margin(top = 30.percent)) {
            Link(
                path = "/",
                text = "home page",
                modifier = NavMenuItemStyle.toModifier(),
                variant = linkVariant
            )
            NavItems()
        }
    }
}

@Composable
fun colors(ifDark: () -> CSSColorValue, ifLight: () -> CSSColorValue): CSSColorValue {
    val colorMode by rememberColorMode()
    return if (colorMode.isDark()) ifDark() else ifLight()
}

@Composable
fun HorizontalNavBar(modifier: Modifier = Modifier) {
    var isMenuShown by remember { mutableStateOf(true) }
    Column(modifier = modifier) {
        Button(
            onClick = { isMenuShown = !isMenuShown },
            NavMenuButtonStyle.toModifier()
        ) {
            FaBars()
        }

        if (isMenuShown) {
            Row(Modifier
                .onClick { isMenuShown = false }
                .flexWrap(FlexWrap.Wrap)
            ) {
                NavItems()
            }
        }
    }
}

@Composable
fun NavItems() {
    val linkVariant = UndecoratedLinkVariant then UncoloredLinkVariant
    Department.values().forEach {
        Link(
            path = when (it) {
//                Department.RADIATION_ONCOLOGY -> "https://www.theabr.org/radiation-oncology/initial-certification/residency-leave-policy"
                else -> "/programs/${it.name.lowercase()}"
            },
            text = it.value.lowercase(),
            modifier = NavMenuItemStyle.toModifier(),
            variant = linkVariant
        )
    }
}

val NavMenuButtonStyle by ComponentStyle.base {
    Modifier
        .fillMaxHeight()
        .margin(5.px)
        .padding(5.px)
}

val NavMenuStyle by ComponentStyle {
    base {
        Modifier
            .userSelect(UserSelect.None)
            .backgroundColor(Colors.LightGray)
    }

    Breakpoint.MD {
        Modifier
            .fillMaxHeight()
            .float(CSSFloat.Left)
    }
}

val NavMenuItemStyle by ComponentStyle {
    base {
        Modifier
            .padding(leftRight = 10.px, topBottom = 5.px)
            .fontFamily(
                "-apple-system", "BlinkMacSystemFont", "Segoe UI", "Roboto", "Oxygen", "Ubuntu",
                "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", "sans-serif"
            )
            .cursor(Cursor.Crosshair)
    }

    hover {
        Modifier.backgroundColor(Colors.DarkGray)
    }
}

val InlineLink by ComponentStyle {
    base {
        Modifier
            .fontFamily(
                "-apple-system", "BlinkMacSystemFont", "Segoe UI", "Roboto", "Oxygen", "Ubuntu",
                "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", "sans-serif"
            )
            .cursor(Cursor.Crosshair)
    }

    hover {
        Modifier.backgroundColor(Colors.DarkGray)
    }
}
