package io.github.rgreb.resleave.pages.programs

import androidx.compose.runtime.Composable
import com.varabyte.kobweb.compose.css.FontWeight
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.fontWeight
import com.varabyte.kobweb.compose.ui.toAttrs
import com.varabyte.kobweb.core.Page
import com.varabyte.kobweb.silk.components.text.SpanText
import io.github.rgreb.resleave.components.layouts.NavLayout
import io.github.rgreb.resleave.components.widgets.CenteredHeaderWithOptionalPDF
import io.github.rgreb.resleave.components.widgets.LiPSpanText
import io.github.rgreb.resleave.components.widgets.fillWidthAndCenterText
import org.jetbrains.compose.web.dom.H3
import org.jetbrains.compose.web.dom.Ol
import org.jetbrains.compose.web.dom.P

@Page("orthopaedic_surgery")
@Composable
fun OrthopaedicSurgery() {
    NavLayout {
        Column() {
            CenteredHeaderWithOptionalPDF(
                "Orthopaedic Surgery",
                "https://www.abos.org/wp-content/uploads/2022/05/Part-I-and-II-RP-2022_05_11.pdf"
            )
            H3(attrs = Modifier.fillWidthAndCenterText().toAttrs()) {
                SpanText("MINIMUM EDUCATIONAL REQUIREMENTS FOR ABOS BOARD CERTIFICATION")
            }
            P {
                SpanText("Time Requirements", modifier = Modifier.fontWeight(FontWeight.Bolder))
                Ol {
                    listOf(
                        "Five years of accredited post-doctoral residency education are required.",
                        "One year must be served in an accredited graduate medical education program whose  " +
                                "curriculum fulfills the content requirements for the PGY-1 (see Section IV.B.l) and is  " +
                                "determined or approved by the director of an accredited orthopaedic surgery residency  " +
                                "program. An additional four years must be served in an accredited orthopaedic surgery  " +
                                "residency program whose curriculum is determined by the director of the accredited  " +
                                "orthopaedic surgery residency.",
                        "Each program may provide individual leave and vacation times for the resident in  " +
                                "accordance with overall institutional policy. However, one year of credit must include  " +
                                "no more than 50 weeks of full-time graduate medical education per year; and at least  " +
                                "46 weeks of full time graduate medical education per year; averaged over five years.  " +
                                "Graduation prior to 60 months from initiation of training is not allowed.",
                        "Program Directors may retain a resident for as long as needed beyond the minimum  " +
                                "required time to ensure the necessary degree of competence in orthopaedic surgery.  " +
                                "According to the current Special Requirements of the RC for Orthopaedic Surgery, the  " +
                                "committee must be notified of such retention. This information must also be provided  " +
                                "to the ABOS through the Record of Residency Assignment program."
                    ).forEach {
                        LiPSpanText(it)
                    }
                }
            }
        }
    }
}