package io.github.rgreb.resleave.pages.programs

import androidx.compose.runtime.Composable
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.toAttrs
import com.varabyte.kobweb.core.Page
import com.varabyte.kobweb.silk.components.text.SpanText
import io.github.rgreb.resleave.components.layouts.NavLayout
import io.github.rgreb.resleave.components.widgets.CenteredHeaderWithOptionalPDF
import io.github.rgreb.resleave.components.widgets.fillWidthAndCenterText
import org.jetbrains.compose.web.dom.H3
import org.jetbrains.compose.web.dom.P

@Page("pmr")
@Composable
fun PMR() {
    NavLayout {
        Column() {
            CenteredHeaderWithOptionalPDF(
                "P M  &  R",
                "https://abpmrprodstorage.file.core.windows.net/pdfs/abpmr_policy_manual.pdf?sv=2019-07-07&sr=s&sig=gvPtafdlDR%2FEE5iksHLdX8QwjHg3FRG7j%2BR6qksTOiM%3D&st=2023-06-12T20%3A09%3A34Z&se=2023-06-12T20%3A24%3A34Z&sp=r#nameddest=5.003"
            )
            H3(attrs = Modifier.fillWidthAndCenterText().toAttrs()) {
                SpanText("Absence From Training")
            }
            P {
                SpanText("This policy applies to all leave time that occurs on July 1, 2021, and after.")
            }
            P {
                SpanText(
                    "Leave time is defined as personal or familial needs, including the birth and care of a newborn, adopted, or " +
                            "foster child (parental leave); care of an immediate family member (child, spouse or parent) with a serious " +
                            "health condition (caregiver leave); the trainees own serious health condition (medical leave), or other " +
                            "planned time away from training (personal leave)."
                )
            }
            P {
                SpanText(
                    "A fellow or resident (trainee) must not be absent from training for more than six weeks (30 working days) " +
                            "annually, except in the case that a special leave period is granted. Regardless of institutional policies " +
                            "regarding absences, any leave time beyond six weeks, excluding a special leave period, must be made up by " +
                            "arrangement with the program director. A trainee may not accumulate leave time or vacation to reduce the " +
                            "overall duration of training."
                )
            }
            P {
                SpanText(
                    "At the sole discretion of the residency Program Director, a one-time special leave period may be granted. " +
                            "This special leave period applies to training prospectively planned as two years in duration and longer. Within " +
                            "a special leave period, a trainee is permitted up to four weeks of additional consecutive leave time to occur " +
                            "as a single event, within the total period of residency training. At the conclusion of a special leave period, any " +
                            "unused additional leave time is forfeited. Deriving only from a Program Director approved special leave " +
                            "period, leave time of up to four weeks is acceptable above and beyond the allowed 6 weeks of leave annually " +
                            "without needing to make up additional training time. Ideally, special leave time should be considered against " +
                            "planned elective rotations, rather than reducing overall training time spent toward core rotations"
                )
            }
        }
    }
}