package io.github.rgreb.resleave.pages.programs

import androidx.compose.runtime.Composable
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.toAttrs
import com.varabyte.kobweb.core.Page
import com.varabyte.kobweb.silk.components.text.SpanText
import io.github.rgreb.resleave.components.layouts.NavLayout
import io.github.rgreb.resleave.components.widgets.CenteredHeaderWithOptionalPDF
import io.github.rgreb.resleave.components.widgets.fillWidthAndCenterText
import org.jetbrains.compose.web.dom.H3
import org.jetbrains.compose.web.dom.H4
import org.jetbrains.compose.web.dom.P

@Page
@Composable
fun Dermatology() {
    NavLayout {
        Column() {
            CenteredHeaderWithOptionalPDF(
                "Dermatology",
                "https://www.abderm.org/public/announcements/effective-july-2021-new-family-leave-policy"
            )
            H3(attrs = Modifier.fillWidthAndCenterText().toAttrs()) {
                SpanText("ABD Policy on Absences in Training Programs of 2 or More Years Duration")
            }
            H4(attrs = Modifier.fillWidthAndCenterText().toAttrs()) {
                SpanText("Effective July 1, 2021")
            }
            P {
                SpanText("The American Board of Dermatology supports parental, caregiver and medical leave during training beyond time allowed for vacation.")
            }
            P {
                SpanText("Departure from completing 36 continuous months of fulltime dermatology training, for example medical leave of absence or care of a newborn / immediate family member, should be documented and justified to the Board through the resident evaluation forms filed annually by the program director.  The approval of such absences is at the discretion of the local program director / clinical competency committee.")
            }
            P {
                SpanText("Absence from training exceeding 8 weeks (6 weeks leave + 2 weeks vacation) in a given year or 16 weeks over three years should be approved only under exceptional circumstances and may necessitate additional training time to ensure that competency requirements are met.  The ABD will rely on the program director to attest when a trainee afforded extra time away from training is competent for initial certification.")
            }
            P {
                SpanText("Time away spent in program-approved conferences designed to develop dermatology-relevant competencies is not to be used in calculating the total time away from training. Residents may have up to 1 week (5 weekdays) per year for educational leave and anything beyond that is at the discretion of the program director.")
            }
        }
    }
}