package io.github.rgreb.resleave.components.widgets

import androidx.compose.runtime.Composable
import androidx.compose.runtime.remember
import com.varabyte.kobweb.compose.css.*
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.silk.components.navigation.Link
import com.varabyte.kobweb.silk.components.style.ComponentStyle
import com.varabyte.kobweb.silk.components.style.hover
import com.varabyte.kobweb.silk.components.style.link
import com.varabyte.kobweb.silk.components.style.visited
import com.varabyte.kobweb.silk.components.text.SpanText
import com.varabyte.kobweb.silk.theme.colors.ColorVar
import org.jetbrains.compose.web.css.Color
import org.jetbrains.compose.web.css.DisplayStyle

@Composable
fun Modifier.flexMax() = this then Modifier.display(DisplayStyle.Flex)

@Composable
fun Modifier.testBg() = this then Modifier.backgroundColor(Color.antiquewhite)
fun Modifier.testBg2() = this then Modifier.backgroundColor(Color.green)

@Composable
fun Modifier.testBg3() = this then Modifier.backgroundColor(Color.aquamarine)
fun Modifier.testBg4() = this then Modifier.backgroundColor(Color.blue)
fun Modifier.testBg5() = this then Modifier.backgroundColor(Color.blueviolet)


fun Modifier.fillWidthAndCenterText() = fillMaxWidth().textAlign(TextAlign.Center)
fun Modifier.underlined() = textDecorationLine(TextDecorationLine.Underline)

val PdfLinkStyle by ComponentStyle {
    base {
        Modifier.color(ColorVar.value())
            .fontSize(FontSize.Small)
            .verticalAlign(VerticalAlign.Super)
    }
    hover {
        Modifier.cursor(Cursor.Crosshair)

    }
    link {
        Modifier.color(ColorVar.value()).textDecorationLine(TextDecorationLine.None)
    }
    visited {
        Modifier.color(ColorVar.value()).textDecorationLine(TextDecorationLine.None)
    }
}

val FileAnIssueStyle by ComponentStyle {
    base {
        Modifier
            .color(ColorVar.value())
            .textDecorationLine(TextDecorationLine.Underline)
    }
    hover {
        Modifier.cursor(Cursor.Crosshair)

    }
    link {
        Modifier.color(ColorVar.value())
    }
    visited {
        Modifier.color(ColorVar.value())
    }
}

typealias SpanComp = @Composable () -> Unit

class SpanSpace {
    val spans = mutableListOf<SpanComp>()
}

@Composable
fun SpanSpace.Normal(modifier: Modifier = Modifier, content: @Composable () -> String) {
    spans.add { SpanText(modifier = modifier, text = content()) }
}

@Composable
fun SpanSpace.Bold(modifier: Modifier = Modifier, content: @Composable () -> String) {
    spans.add { SpanText(modifier = modifier then Modifier.fontWeight(FontWeight.Bold), text = content()) }
}

@Composable
fun SpanSpace.InlineLink(path: String, modifier: Modifier = Modifier, content: @Composable () -> String) {
    spans.add { Link(modifier = modifier, path = path, text = content()) }
}

@Composable
fun InlineText(modifier: Modifier = Modifier, content: @Composable SpanSpace.(Modifier) -> Unit) {
    val space = remember { SpanSpace() }
    with(space) {
        content(modifier)
        spans.forEach { it() }
    }
}