package io.github.rgreb.resleave.pages.programs

import androidx.compose.runtime.Composable
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.toAttrs
import com.varabyte.kobweb.core.Page
import com.varabyte.kobweb.silk.components.text.SpanText
import io.github.rgreb.resleave.components.layouts.NavLayout
import io.github.rgreb.resleave.components.widgets.CenteredHeaderWithOptionalPDF
import io.github.rgreb.resleave.components.widgets.fillWidthAndCenterText
import org.jetbrains.compose.web.dom.H3
import org.jetbrains.compose.web.dom.P

@Page
@Composable
fun Ophthalmology() {
    NavLayout {
        Column() {
            CenteredHeaderWithOptionalPDF(
                "Ophthalmology",
                "https://www.manula.com/manuals/american-board-of-ophthal/abo-rules-regulations-amp-policies/1/en/topic/leave-of-absence-policy-for-residents"
            )
            H3(attrs = Modifier.fillWidthAndCenterText().toAttrs()) {
                SpanText("Leave of Absence Policy for Residents")
            }
            P {
                SpanText("Leaves of absence for vacation, medical issues, parental leave, or other personal reasons are essential for resident well-being and may be granted at the discretion of the institutions department chair and/or residency program director.")
            }
            P {
                SpanText("Residency training in ophthalmology traditionally comprises 48 months following graduation from medical school, including a 12-month internship and at least 36 months of ACGME-accredited ophthalmology training. The ABO requires that a satisfactory rating be confirmed by the residency program for every candidate in each of the ACGME competencies. Depending on the length of absence or the inability to accomplish residency educational goals during the traditional period, the required time for graduate medical education may be extended accordingly.")
            }
            P {
                SpanText("Residency program leadership and the institutional graduate medical education offices, not the ABO, determine the need for any extension of residency training and the ultimate completion date for each resident. However, less than six months of training at any PGY level is not acceptable for board certification.")
            }
        }
    }
}