package io.github.rgreb.resleave.pages.programs

import androidx.compose.runtime.Composable
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.toAttrs
import com.varabyte.kobweb.core.Page
import com.varabyte.kobweb.silk.components.text.SpanText
import io.github.rgreb.resleave.components.layouts.NavLayout
import io.github.rgreb.resleave.components.widgets.CenteredHeaderWithOptionalPDF
import io.github.rgreb.resleave.components.widgets.fillWidthAndCenterText
import org.jetbrains.compose.web.dom.H3
import org.jetbrains.compose.web.dom.P

@Page
@Composable
fun Otolaryngology() {
    NavLayout {
        Column() {
            CenteredHeaderWithOptionalPDF(
                "Otolaryngology",
                "https://www.abohns.org//sites/default/files/Images/PDF/PARENTAL%2C%20CAREGIVER%2C%20MEDICAL%20LEAVE.pdf"
            )
            H3(attrs = Modifier.fillWidthAndCenterText().toAttrs()) {
                SpanText("PARENTAL, CAREGIVER and MEDICAL LEAVE DURING TRAINING POLICY")
            }
            P {
                SpanText(
                    "Leaves of absence are defined as requests for time away from accredited training for parental,  " +
                            "caregiver, or personal medical leave. Leave of absence requests may be granted to residents at  " +
                            "the discretion of the Program Director in accordance with local rules. For Board eligibility, the  " +
                            "total of such leaves and vacation may not exceed six weeks in any one post-graduate year  " +
                            "(PGY) with the following exception: for one year during residency training, a resident can take  " +
                            "parental, caregiver, or medical leave for up to six weeks with the balance of time up to a total of  " +
                            "eight weeks (leaves plus vacation) used for vacation time. Fellows in one- or two year ACGMEaccredited fellowship programs may not exceed six weeks of such leaves and vacation in any  " +
                            "training year."
                )
            }
            P {
                SpanText(
                    "Attendance at academic conferences or training courses does not contribute to the yearly  " +
                            "allotment of leave. Mission trips count toward the yearly time away allotment. This policy defines  " +
                            "a week as seven consecutive days."
                )
            }
            P {
                SpanText(
                    "If a circumstance occurs in which a residents or fellows absence exceeds the allotted time  " +
                            "outlined in this ABOHNS policy, the Program Director must submit a plan to the ABOHNS for  " +
                            "approval on how the necessary training will be achieved, which may require an extension of the  " +
                            "residency or fellowship. For residents, training time from prior PGY-year levels cannot be  " +
                            "applied to subsequent PGY-year time deficiencies. Surrogates of competency (i.e. subjective  " +
                            "evaluations, case volumes, ACGME Milestones) cannot be used as substitutes for the time  " +
                            "requirements described in this policy. "
                )
            }
        }
    }
}