package io.github.rgreb.resleave.pages.programs

import androidx.compose.runtime.Composable
import com.varabyte.kobweb.compose.css.FontSize
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.fontSize
import com.varabyte.kobweb.compose.ui.modifiers.margin
import com.varabyte.kobweb.compose.ui.toAttrs
import com.varabyte.kobweb.core.Page
import com.varabyte.kobweb.silk.components.text.SpanText
import io.github.rgreb.resleave.components.layouts.NavLayout
import io.github.rgreb.resleave.components.widgets.*
import org.jetbrains.compose.web.css.percent
import org.jetbrains.compose.web.dom.H3
import org.jetbrains.compose.web.dom.Ol
import org.jetbrains.compose.web.dom.P

@Page("plastic_surgery")
@Composable
fun PlasticSurgery() {
    NavLayout {
        Column {
            CenteredHeaderWithOptionalPDF(
                "Plastic Surgery",
                "https://www.abplasticsurgery.org/media/19183/Personal-Leave-Policy-Approved-5-2021.pdf"
            )
            H3(attrs = Modifier.fillWidthAndCenterText().toAttrs()) {
                SpanText("Personal Leave Policy")
            }
            P {
                InlineText {
                    Normal {
                        "The American Board of Plastic Surgery (ABPS) (Board) has established an optional 12 weeks of Personal " +
                                "Leave that is available to residents in "
                    }
                    Bold {
                        "Integrated, Independent and Competency-Based plastic surgery"
                    }
                    Normal {
                        " residency training programs. The Personal Leave Policy went into effect beginning with the 2019-2020 academic year."
                    }
                }
            }

            P {
                InlineText {
                    Bold {
                        "The 12 weeks of Personal Leave may only be used for maternity leave, paternity leave, medical leave, foster care, adoption, family leave or elective rotations (both international and domestic*)."
                    }
                    Normal {
                        "Personal " +
                                "Leave can be used exclusively for leave as defined by the Board, exclusively for rotations or any " +
                                "combination of both."
                    }
                    Bold {
                        "These 12 weeks of Personal Leave replaced the previous 12-week block of " +
                                "elective rotations."
                    }
                    Normal {
                        " Personal Leave is not required to be taken as a single block, but can be distributed throughout the entire residency as the training program allows. Effective July 1, 2021, no more than 4weeks of personal leave can be taken during the last 3 months of plastic surgery residency training. " +
                                "Personal Leave is not to be used for travel and moving activities related to transition to a new residency " +
                                "position, fellowship training or new practice. Residents must use the 4 non-clinical weeks allotted per " +
                                "year for moving activities. "
                    }
                }
            }

            P {
                SpanText(
                    "To monitor resident safety, all international and individual domestic* rotations, whether full clinical or  " +
                            "observational, must be approved by the Program Director and the ABPS. In addition, all international  " +
                            "rotations must be approved by the Plastic Surgery Residency Review Committee (RRC-PS). Refer to the  " +
                            "Boards requirements for international rotations."
                )
            }

            P(attrs = Modifier.margin(left = 10.percent).toAttrs()) {
                SpanText(
                    modifier = Modifier.fontSize(FontSize.Smaller),
                    text = "*Domestic rotations that are a standard part of the programs curriculum (all residents in the  " +
                            "program will complete that rotation and the training program has a Program Letter of  " +
                            "Agreement (PLA) for that rotation) do not require Board approval. Domestic rotations that are  " +
                            "specific to an individual will require Board approval under the Personal Leave Policy if not all  " +
                            "residents will complete that rotation during training.  "
                )
            }
            P {
                InlineText {
                    Normal {
                        "Personal Leave is considered independent of research time (6 weeks for Independent and 12 weeks for  " +
                                "Integrated)."
                    }
                    Bold {
                        " Residents may not use research time for Personal Leave."
                    }
                }
            }
            P {
                SpanText(
                    "To receive approval for Personal Leave, the Program Director must provide details regarding the leave  " +
                            "request and explain how the combination of Personal Leave and non-clinical weeks still allows the  " +
                            "resident to accomplish the 48 clinical weeks per year that the Board requires. The 12 weeks of Personal Leave, whether used for maternity, paternity, medical, family, foster care, adoption or elective rotations  " +
                            "will count towards the 48 clinical weeks required per year. The 4 non-clinical weeks per year do not  " +
                            "count towards the 48 clinical weeks per year. The Board does not define the remaining 4 weeks per year  " +
                            "beyond the 48 weeks of required clinical training and therefore those weeks may be used for vacation,  " +
                            "medical leave, rotations or any activity as determined by the local institution and/or program. Personal  " +
                            "Leave taken beyond the combination of 12 weeks of personal leave and the 4 mon-clinical weeks per  " +
                            "year, averaged over the residency, would result in extended plastic surgery residency training.  "
                )
            }
            P {
                InlineText {
                    Normal {
                        "Program Directors are required to attest that the resident satisfactorily completed all Plastic Surgery  " +
                                "Milestones. This attestation will be completed by the Program Director at the time the Residency  " +
                                "Graduation Form is requested by the Board and the Program Director officially signs off on the  " +
                                "graduating resident. Program Directors (not residents) must contact the Board in writing for approval of  " +
                                "any Personal Leave. The Program Director must send a letter to the Board Office via email to"
                    }
                    InlineLink(path = "mailto:info@abplasticsurgery.org") {
                        "info@abplasticsurgery.org"
                    }
                    Normal {
                        "detailing the following:"
                    }
                }
                Ol {
                    LiPSpanText("Name of resident;")
                    LiPSpanText("Reason for personal leave;")
                    LiPSpanText("Exact dates of expected personal leave;")
                    LiPSpanText("Number of weeks of any previously used and ABPS approved personal leave;")
                    LiPSpanText("Number of clinical weeks worked and expected for each year of the residents training;")
                    LiPSpanText("PSY level of training that personal leave will be taken.")
                }
            }
        }
    }
}