package io.github.rgreb.resleave.pages.programs

import androidx.compose.runtime.Composable
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.core.Page
import com.varabyte.kobweb.silk.components.text.SpanText
import io.github.rgreb.resleave.components.layouts.NavLayout
import io.github.rgreb.resleave.components.widgets.CenteredHeaderWithOptionalPDF
import org.jetbrains.compose.web.dom.H3
import org.jetbrains.compose.web.dom.Li
import org.jetbrains.compose.web.dom.P
import org.jetbrains.compose.web.dom.Ul

@Page("anesthesiology")
@Composable
fun Anesthesiology() {
    NavLayout {
        Column() {
            CenteredHeaderWithOptionalPDF(
                "Anesthesiology",
                "https://www.theaba.org/wp-content/uploads/pdfs/Policy_Book.pdf"
            )

            H3 {
                SpanText("3.03 -- Absence From Training".uppercase())
            }
            P {
                SpanText("We have established certain training requirements for a candidate to enter our examination system. The following outlines permissible absences that will not result in delay in a candidate being eligible to enter the examination system:")
                Ul {
                    AnesthesiologyText.bulletPoints.forEach {
                        Li {
                            P {
                                SpanText(it)
                            }
                        }
                    }
                    Ul {
                        AnesthesiologyText.subPoints.forEach {
                            Li {
                                P {
                                    SpanText(it)
                                }
                            }
                        }
                    }
                }
            }
            P {
              SpanText("Absences in excess of those described above will require lengthening the total training time to compensate for the additional absences from training. The additional training days required will be equal to the total number of working days missed beyond (1) the 60 working days allowed during the CA1-3 years and the 20 working days allowed during the CB year; and (2) the additional 40 working days if approved by the ABA"
              )
            }
            P {
                SpanText("Residents who have their residency extended may take the summer ADVANCED Examination if they complete all requirements by Sept. 30 of the same year. They may take the winter ADVANCED Examination if they complete all requirements by March 30 of the same year.")
            }
            P {
                SpanText("A lengthy interruption in training may have a deleterious effect upon the resident's knowledge or clinical competence. Therefore, when there is an absence for a period more than six months, our Credentials Committee shall determine the number of months of training the resident must complete subsequent to resumption of the residency program to satisfy the training required for admission to the ABA examination system.")
            }
        }
    }
}

object AnesthesiologyText {
    val bulletPoints = listOf(
        "Without prior approval from the ABA, a candidate may be absent from training up to a total of 60 working days (12 weeks) during the CA 1-3 years of training. During the CB year, the candidate may be absent from training up to a total of 20 working days (4 weeks) for vacation/sick days, as permitted by the policy of the candidate's institution and/or department",
        "Attendance at scientific meetings, not to exceed five working days per year, and the day of the BASIC Examination shall be considered part of the training program and not count toward the absence calculation.",
        "Candidates should also comply with the policy of the institution and department in which that portion of the training is served for the duration of any absence during the clinical base year.",
        "We will consider requests for up to 40 additional days (eight weeks) away from training (over and above the 60 working days). Such additional leave of absence time must be approved by the ABA as follows:"
    )

    val subPoints = listOf(
        "Any request for such leave must be received by our office within four weeks of the resident's resumption of the residency program.",
        "The request shall be in writing from the program director, countersigned by the department chair (if that person is different than the program director), and the resident.",
        "The request must include: (1) the reason for the absence training request (serious medical illness, military family leave, parental or family leave covered under the Family and Medical Leave Act would be reasons acceptable to the ABA) and (2) documentation about how all clinical experiences and educational objectives will be met."
    )
}