package io.github.rgreb.resleave.pages.programs

import androidx.compose.runtime.Composable
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.toAttrs
import com.varabyte.kobweb.core.Page
import com.varabyte.kobweb.silk.components.text.SpanText
import io.github.rgreb.resleave.components.layouts.NavLayout
import io.github.rgreb.resleave.components.widgets.*
import org.jetbrains.compose.web.dom.*

@Page("radiology")
@Composable
fun Radiology() {
    NavLayout {
        Column() {
            CenteredHeaderWithOptionalPDF(
                "Radiology",
                "https://www.theabr.org/exam-details/residency-leave-policy"
            )
            H3(attrs = Modifier.fillWidthAndCenterText().toAttrs()) {
                SpanText("Residency Leave Policy")
            }
            H4 {
                SpanText("Training requirement for Initial Certification")
            }
            P {
                SpanText("The standard training requirement for Initial Certification is successful completion of a residency accredited by the ACGME or RCPSC (Radiation Oncology, Diagnostic Radiology, and Interventional Radiology) or CAMPEP (Medical Physics). ")
            }
            P {
                SpanText("The ABR recognizes that personal wellness and family needs are important considerations during residency and supports residency training that allows for adequate vacation in addition to other leave, including parental, caregiver, and medical leave.")
            }
            P {
                InlineText {
                    Bold {
                        "Beginning with the  2021-2022 academic year, residents will be considered eligible for Initial Certification without an extension of training with Time Off that does not exceed an average of eight weeks (40 workdays) per academic year over the duration of the residency."
                    }
                }
            }
            P {
                SpanText("The maximal Time Off defined by this policy is meant to apply to exceptional circumstances. This policy is not intended to define the typical Time Off that a resident should expect, nor the typical Time Off that a program should provide. Successful completion of residency training and achieving clinical competency will, in many cases, require more training time during residency with or without adjunct efforts at the discretion of the PD. Completion of the training period is a necessary but not sufficient element to establish competency.")
            }
            P {
                SpanText("A resident with Time Off that exceeds an average of eight weeks (40 workdays) per training year will require an extension of training beyond the planned graduation date to be considered eligible for Initial Certification. The length of such extension will be determined by the Program Director (PD) but, at a minimum, must equal the excess number of workdays missed during the training period.")
            }
            P {
                SpanText("Eligibility for Initial Certification requires the program director (PD) to attest to successful completion of the residency and competency. Irrespective of Time Off, PD should extend the length of training if a resident has not achieved competency.")
            }
            P {
                SpanText("Residents who began training prior to 2021 may apply to the ABR, with the approval of their PD, for an exemption to this new policy. Circumstances warranting such exceptions are expected to be uncommon.")
            }
            P {
                SpanText("The following are considered Time Off:")
                Ol {
                    LiPSpanText("Vacation")
                    LiPSpanText("Bereavement leave")
                    LiPSpanText("Interview days away from the institution")
                    LiPSpanText("Parental leave")
                    LiPSpanText("Caregiver leave")
                    LiPSpanText("Medical leave (sick time)")
                    LiPSpanText("Other leave as determined by the PD")
                }
                Br {}
                SpanText("The following are considered part of the residency (rather than Time Off):")
                Ol {
                    listOf(
                        "Research activities approved as part of the residency by the PD",
                        "Attendance at a scientific or educational meeting in, or related to, the specialty",
                        "Days linked to off hours work (for example, a Monday off following a weekend call)",
                        "Workdays performed off site, including those resulting from a pandemic, natural disaster, etc."
                    ).forEach {
                        LiPSpanText(it)
                    }
                }
            }
            P {
                InlineText { Bold { "Notes" } }
                listOf(
                    "Initial Certification via the training pathway requires passing requisite exams as well as successfully completing an accredited residency.",
                    "Residency PDs will need to attest to both successful completion of training and competency to allow applicants to sit for Initial Certification exams.",
                    "The ABR does not define how much time can or should be permitted by the program or institution; instead, this policy is intended to delineate Time Off limits only as those limits define satisfactory completion of the residency in relation to board eligibility.",
                    "This ABR policy does not supersede local policy with regard to the amount of leave time provided, which in many cases may be more limited than our policy allows.",
                    "Leaves of absence and vacations may be granted to residents at the discretion of the PD in accordance with their institutions rules.",
                    "Residency PDs and their institutional GME offices have the discretion to extend residency training as needed to allow adequate determination of competency (and attest to successful completion) for an individual trainee.",
                    "Per ABMS guidance, this policy applies to training programs of two or more years. "
                ).forEach {
                    P {
                        SpanText(it)
                    }
                }
            }
        }
    }
}