package io.github.rgreb.resleave.pages.programs

import androidx.compose.runtime.Composable
import com.varabyte.kobweb.compose.css.FontSize
import com.varabyte.kobweb.compose.css.FontWeight
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.fontSize
import com.varabyte.kobweb.compose.ui.modifiers.fontWeight
import com.varabyte.kobweb.core.Page
import com.varabyte.kobweb.silk.components.navigation.Link
import com.varabyte.kobweb.silk.components.text.SpanText
import io.github.rgreb.resleave.components.layouts.NavLayout
import io.github.rgreb.resleave.components.widgets.CenteredHeaderWithOptionalPDF
import org.jetbrains.compose.web.dom.Br
import org.jetbrains.compose.web.dom.H3
import org.jetbrains.compose.web.dom.P

@Page
@Composable
fun Neurology(modifier: Modifier = Modifier) {
    NavLayout {
        Column(modifier) {
            CenteredHeaderWithOptionalPDF(
                headerText = "Neurology",
                url = "https://www.abpn.com/wp-content/uploads/2023/04/Policy-Regarding-Training.pdf"
            )
            H3 {
                SpanText("Training")
            }
            P {
                SpanText("Unless otherwise specified, all training must be completed in programs accredited by the ACGME.")
            }
            P {
                SpanText("For additional training information, see the individual Information for Applicants document for a specific exam.")
            }
            P {
                SpanText("Leave of Absence Policy:", modifier = Modifier.fontSize(FontSize.Large).fontWeight(FontWeight.Bold))
                Br{}
                SpanText(
                    "The ABPN requires that all training programs allow, at minimum, an average of four weeks of leave time " +
                            "(including vacation, sick time, parental or caregiver leave, etc.) during training per year. Leave or vacation " +
                            "time may NOT be used to reduce the total amount of required residency training or to make up deficiencies " +
                            "in training. "
                )
            }
            P {
                SpanText(
                    "Also, training programs must allow, at least once during training, a minimum of six weeks of time away from " +
                            "training for purposes of parental, caregiver, and medical leave, without exhausting all other allowed time " +
                            "away from training and without extending training. Within ABPN and ACGME policy guidelines, it is up to the " +
                            "program director and the program clinical competency committee to determine whether a given resident " +
                            "has met training requirements or must extend their period of training."
                )
            }
            P {
                SpanText("The ABPN leave of absence policy does not supersede institutional policies and applicable laws.")
            }

            P {
                SpanText("Please check the ")
                Link(
                    text = "Leave of Absence Policy FAQs",
                    path = "https://www.abpn.com/wp-content/uploads/2023/04/LOA-FAQ.pdf"
                )
                SpanText(" document on the ABPN website. ")
            }
        }
    }
}