package io.github.rgreb.resleave.components.layouts

import androidx.compose.runtime.Composable
import com.varabyte.kobweb.compose.css.FontSize
import com.varabyte.kobweb.compose.foundation.layout.Box
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.foundation.layout.Row
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.compose.ui.toAttrs
import com.varabyte.kobweb.silk.components.style.breakpoint.Breakpoint
import com.varabyte.kobweb.silk.components.style.toAttrs
import com.varabyte.kobweb.silk.components.text.SpanText
import com.varabyte.kobweb.silk.theme.breakpoint.rememberBreakpoint
import io.github.rgreb.resleave.components.sections.HorizontalNavBar
import io.github.rgreb.resleave.components.sections.VerticalNavBar
import io.github.rgreb.resleave.components.widgets.FileAnIssueStyle
import io.github.rgreb.resleave.components.widgets.colorSwapButton
import io.github.rgreb.resleave.components.widgets.fillWidthAndCenterText
import org.jetbrains.compose.web.css.LineStyle
import org.jetbrains.compose.web.css.px
import org.jetbrains.compose.web.dom.A
import org.jetbrains.compose.web.dom.Div
import org.jetbrains.compose.web.dom.Text

@Composable()
fun NavLayout(content: @Composable () -> Unit) {
    Column() {
        val breakpoint = rememberBreakpoint()
        if (breakpoint >= Breakpoint.MD)
            Row(modifier = Modifier.maxWidth(1000.px)) {
                Column {
                    colorSwapButton()
                    VerticalNavBar()
                }
                Column(
                    modifier = Modifier.fillMaxWidth().padding(left = 64.px)
                ) {
                    content()
                    FileAnIssue()
                }
            }
        else {
            Column(modifier = Modifier.fillMaxWidth()) {
                Row(modifier = Modifier.fillMaxWidth()) {
                    Box(modifier = Modifier.fillMaxWidth()) {
                        HorizontalNavBar()
                    }
                    Box {
                        colorSwapButton(Modifier.align(Alignment.End))
                    }
                }
                content()
            }
        }
    }
}

@Composable
fun FileAnIssue() {
    Div(
        attrs = Modifier
            .borderTop(1.px, style = LineStyle.Solid)
            .padding(12.px)
            .fillWidthAndCenterText()
            .fontSize(FontSize.Smaller)
            .toAttrs()
    ) {
        Text("If something looks wrong, please ")
        A(
            href = "https://github.com/rgreb/rgreb.io/issues",
            attrs = FileAnIssueStyle.toAttrs()

        ) {
            SpanText("submit an issue.")
        }
        Text(" This site was built with ")
        A(
            href = "https://github.com/varabyte/kobweb",
            attrs = FileAnIssueStyle.toAttrs()

        ) {
            SpanText("Kobweb.")
        }
    }
}