package io.github.rgreb.resleave.pages.programs

import androidx.compose.runtime.Composable
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.core.Page
import com.varabyte.kobweb.silk.components.text.SpanText
import io.github.rgreb.resleave.components.layouts.NavLayout
import io.github.rgreb.resleave.components.widgets.Bold
import io.github.rgreb.resleave.components.widgets.CenteredHeaderWithOptionalPDF
import io.github.rgreb.resleave.components.widgets.InlineText
import io.github.rgreb.resleave.components.widgets.Normal
import org.jetbrains.compose.web.dom.H3
import org.jetbrains.compose.web.dom.P

@Page("urology")
@Composable
fun Urology() {
    NavLayout {
        Column() {
            CenteredHeaderWithOptionalPDF(
                "Urology",
                "https://www.abu.org/residency-requirements/"
            )
            H3 {
                SpanText("Leaves of Absence")
            }
            P {
                InlineText {
                    Normal {
                        "Leaves of absence and vacation may be granted to residents at the discretion of the Program Director consistent with local institutional policy and applicable laws.  Each program may provide vacation leave and family leave ("
                    }
                    Bold {
                        "any leave required to care for a family member"
                    }
                    Normal {
                        ") for the resident in accordance with institutional policy.  The ABU requires 46 weeks of full-time clinical activity in each of the five years of residency.  However, the 46 weeks may be averaged over the first 3 years of residency, for a total of 138 weeks required in the first 3 years, and over the last 2 years, a total of 92 weeks is required. "
                    }
                }
            }
            P {
                SpanText("Vacation or various other leave may not be accumulated to reduce the total training requirement. Should circumstances occur which keep a resident from working the required 138 weeks the first 3 years and 92 weeks the last 2 years, the Program Director must submit a request to the ABU for a variance of the current policy or a plan outlining how the training deficit will be rectified.  In certain cases an extension of the residency training may be required.")
            }
            P {
                SpanText("This policy is not retroactive and does not apply to leave taken prior to the 2021-2022 academic year.")
            }
            P {
                SpanText("Ninety-two (92) weeks of training is required for two-year fellowships, without the need to request a variance or submit a plan for making up a training deficit. ")
            }
            P {
                SpanText("Leave for educational/scientific conferences are at the discretion of the Program Director.")
            }
        }
    }
}