package io.github.rgreb.resleave.pages.programs

import androidx.compose.runtime.Composable
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.core.Page
import com.varabyte.kobweb.silk.components.text.SpanText
import io.github.rgreb.resleave.components.layouts.NavLayout
import io.github.rgreb.resleave.components.widgets.*
import org.jetbrains.compose.web.dom.H3
import org.jetbrains.compose.web.dom.H4
import org.jetbrains.compose.web.dom.P

@Page("psychiatry")
@Composable
fun Psychiatry() {
    NavLayout {
        Column() {
            CenteredHeaderWithOptionalPDF(
                "Psychiatry",
                "https://www.abpn.com/wp-content/uploads/2023/04/Policy-Regarding-Training.pdf"
            )
            H3 {
                SpanText("Training")
            }

            P {
                SpanText(
                    "Unless otherwise specified, all training must be completed in programs accredited by the ACGME.  " +
                            "For additional training information, see the individual Information for Applicants document for a specific  " +
                            "exam"
                )
            }
            H4{
                InlineText { Bold { "Leave of Absence Policy:" } }
            }
            P {
                SpanText(
                    "The ABPN requires that all training programs allow, at minimum, an average of four weeks of leave time\n" +
                            "(including vacation, sick time, parental or caregiver leave, etc.) during training per year. Leave or vacation\n" +
                            "time may NOT be used to reduce the total amount of required residency training or to make up deficiencies\n" +
                            "in training."
                )
            }
            P {
                SpanText(
                    "Also, training programs must allow, at least once during training, a minimum of six weeks of time away from\n" +
                            "training for purposes of parental, caregiver, and medical leave, without exhausting all other allowed time\n" +
                            "away from training and without extending training. Within ABPN and ACGME policy guidelines, it is up to the\n" +
                            "program director and the program clinical competency committee to determine whether a given resident\n" +
                            "has met training requirements or must extend their period of training."
                )
            }
        }
        P {
            SpanText("The ABPN leave of absence policy does not supersede institutional policies and applicable laws.")
        }

        P {
            InlineText {
                Normal { "Please check the " }
                InlineLink(path = "https://www.abpn.com/wp-content/uploads/2023/04/LOA-FAQ.pdf") { "Leave of Absence Policy FAQs" }
                Normal { " document on the ABPN website." }
            }
        }
    }
}
