package io.github.rgreb.resleave.pages.programs

import androidx.compose.runtime.Composable
import com.varabyte.kobweb.compose.css.FontSize
import com.varabyte.kobweb.compose.css.FontStyle
import com.varabyte.kobweb.compose.css.FontWeight
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.foundation.layout.Row
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.compose.ui.toAttrs
import com.varabyte.kobweb.core.Page
import com.varabyte.kobweb.silk.components.text.SpanText
import io.github.rgreb.resleave.components.layouts.NavLayout
import io.github.rgreb.resleave.components.sections.colors
import io.github.rgreb.resleave.components.widgets.*
import org.jetbrains.compose.web.css.Color
import org.jetbrains.compose.web.css.FlexWrap
import org.jetbrains.compose.web.css.LineStyle
import org.jetbrains.compose.web.css.px
import org.jetbrains.compose.web.dom.Br
import org.jetbrains.compose.web.dom.H3
import org.jetbrains.compose.web.dom.H4
import org.jetbrains.compose.web.dom.P

@Page
@Composable
fun Surgery() {
    NavLayout {
        Column(modifier = Modifier.flexWrap(FlexWrap.Wrap)) {
            CenteredHeaderWithOptionalPDF(
                "Surgery",
                "https://www.absurgery.org/default.jsp?policygsleave"
            )
            H3(attrs = Modifier.fillWidthAndCenterText().toAttrs()) {
                SpanText("Leave Policy - General Surgery")
            }
            H4 {
                SpanText("General Requirements")
            }
            Row(modifier = Modifier.fillMaxWidth().flexMax()) {
                Column {
                    P {
                        InlineText {
                            Normal { "In general, the ABS requires " }
                            Bold { "48 weeks of full-time clinical activity " }
                            Normal {
                                "in each of the five years of residency, regardless of the amount of operative experience obtained. The remaining four weeks of the year are considered non-clinical time that may be used for any purpose, such as vacation, conferences, interviews, etc."
                            }
                        }
                    }
                    P {
                        SpanText("All time away from clinical activity (i.e., non-clinical time), including vacation and time taken for interviews, visa issues, etc., must be accounted for on the application for certification.")
                    }
                }
                P(attrs = Modifier.padding(5.px)
                    .fontSize(FontSize.Small)
                    .maxWidth(200.px)
                    .margin(left = 8.px)
                    .borderLeft(
                        width = 2.px,
                        style = LineStyle.Solid,
                        color = colors(
                            ifDark = { Color.whitesmoke },
                            ifLight = { Color.black }
                        ))
                    .toAttrs()) {
                    InlineText {
                        Normal {
                            "This leave policy allows for flexible caregiver and parental leave, and is in accordance with the "
                        }
                        InlineLink("https://www.abms.org/policies/parental-leave/") {
                            " policy guidelines "
                        }
                        Normal {
                            "established by the American Board of Medical Specialties (ABMS), as well as the "
                        }
                        InlineLink("https://www.acgme.org/globalassets/pfassets/programrequirements/800_institutionalrequirements_2022_tcc.pdf") {
                            "Institutional Requirements"
                        }
                        Normal {
                            " established by the Accreditation Council for Graduate Medical Education (ACGME)."
                        }
                    }
                }
            }
            H3 {
                SpanText("ABS General Surgery Family Leave Policy")
            }
            P {
                InlineText {
                    Normal { "The ABS acknowledges the need to take time away from training for certain significant life events. " }
                    Bold { "Effective as of the 2021-2022 academic year and thereafter, " }
                    Normal { "as allowed by their programs, residents may take " }
                    Bold { "documented leave" }
                    Normal { " to care for a new child, whether for the birth, the adoption, or placement of a child in foster care; to care for a seriously ill family member (partner, child, or parent); to grieve the loss of a family member (partner, child, or parent); or to recover from the resident's own serious illness. " }
                }
                InlineText(modifier = Modifier.fontStyle(FontStyle.Italic)) {
                    Normal { "This policy is " }
                    Bold { "not retroactive " }
                    Normal { "and does not apply to leave taken prior to the 2021-2022 academic year." }
                }
            }
            P {
                InlineText {
                    Normal { "Residents may take an additional four (4) weeks off during the first three (3) years of residency, for a total of " }
                    Bold { "140 weeks" }
                    Normal { " required, and an additional four (4) weeks off during the last two (2) years of residency, for a total of " }
                    Bold { "92 weeks" }
                    Normal { " required, all while maintaining admissibility to the ABS initial certification examination process. " }
                    Bold { "Residents are expected to complete a minimum of 48 weeks of Chief Resident rotations" }
                    Normal { " (e.g., a resident may accomplish this by logging chief cases as a PGY-4)." }
                }
                Br {}
                InlineText(modifier = Modifier.fontStyle(FontStyle.Italic)) {
                    Normal { "Note: This is an ABS policy " }
                    Bold { "only" }
                    Normal { "  and should not be confused with family leave as permitted by the Family and Medical Leave Act (FMLA). " }
                    Bold { "No approval is needed for this option if taken as outlined." }
                }
            }
            SurgerySection("Additional Leave Options") {
                SpanText("While the ABS considers the 48-week requirement to be a valuable standard in developing fully-trained surgeons, options are available to provide programs and residents with some flexibility.")
            }

            SurgerySection("Averaging") {
                InlineText {
                    Normal { "The 48 weeks " }
                    Bold { "may be averaged" }
                    Normal { " over the first 3 years of residency, for a total of " }
                    Bold { "144 weeks" }
                    Normal { " required in the " }
                    Bold { "first 3 years," }
                    Normal { "and over the last 2 years, for a total of " }
                    Bold { "96 weeks" }
                    Normal { " required in the " }
                    Bold { "last 2 years" }
                    Normal { "Thus, non-clinical time may be reduced in one year to allow for additional non-clinical time in another year." }
                }
            }

            SurgerySection("Extending Chief Year")
            {
                InlineText {
                    Normal { "The ABS also permits, " }
                    Bold { "with advance approval" }
                    Normal { " the five clinical years of residency to be " }
                    Bold { "completed over a six-year period" }
                    Normal { " (\"6-Year Option\"). See farther below for more details." }
                }
            }

            SurgerySection("Other Arrangements") {
                InlineText {
                    Normal { "The ABS will also consider other arrangements beyond what is noted above on a case-by-case basis. These will also require " }
                    Bold { "advance approval." }
                }
            }
            P {
                InlineText {
                    Bold { "All requests" }
                    Normal { " for approval must be made by the program director (not the resident) and must be sent by" }
                    Bold { " mail or fax on official letterhead to the ABS office (no emails)." }
                }
            }
            P(attrs = Modifier.border(
                width = 1.px,
                style = LineStyle.Solid,
                color = colors(
                    ifDark = { Color.whitesmoke },
                    ifLight = { Color.black }
                ))
                .borderLeft(
                    8.px, style = LineStyle.Solid,
                    color = colors(
                        ifDark = { Color.whitesmoke },
                        ifLight = { Color.black })
                )
                .padding(12.px)
                .toAttrs()
            ) {
                InlineText {
                    Bold { "Program directors:" }
                    Normal { " When making a request for other arrangements, please include in your letter a complete schedule of the resident's training, with calendar dates, including all leave time." }
                }
            }
            SurgerySection("6-Year Option") {
                InlineText {
                    Normal { "If permitted by the residency program, the five clinical years of residency training may be completed" }
                    Bold { "over six academic years." }
                    Normal { " All training must be completed at a single program with " }
                    Bold { "advance approval from the ABS." }
                    Normal { " In this option, an average of 48 weeks of full-time training is required in each clinical year as explained above. The first 12 months of clinical training would be counted as PGY-1, the second 12 months as PGY-2, and so forth. No block of clinical training may be shorter than one month (four weeks)." }
                }
                P {
                    SpanText("Use of the six-year option is solely at the program's discretion, and contingent on advance approval from the ABS. The option may be used for any purpose approved by the residency program, including but not limited to, family issues, visa issues, medical problems, maternity leave, external commitments, volunteerism, pursuit of outside interests, educational opportunities, etc.")
                }
                P {
                    InlineText {
                        Normal { "For questions regarding this policy, please contact the " }
                        InlineLink("https://www.absurgery.org/app.jsp?type=email&id=6&to=2&subject=Question%20about%20General%20Surgery%20Leave%20Policy") { "ABS coordinator." }
                    }
                }
            }
            SurgerySection("Previous ABS General Surgery Family Leave Policy") {
                InlineText {
                    Bold { "Prior to the 2021-2022 academic year," }
                    Normal { " as allowed by their programs, residents could take " }
                    Bold { "documented leave" }
                    Normal { " to care for a new child, whether for the birth, the adoption, or placement of a child in foster care; to care for a seriously ill family member (spouse, son, daughter, or parent); to grieve the loss of a family member (spouse, son, daughter, or parent); or to recover from the residents own serious illness. " }
                    Normal(modifier = Modifier.fontStyle(FontStyle.Italic)) { "This policy was " }
                    Bold(modifier = Modifier.fontStyle(FontStyle.Italic)) { "not retroactive" }
                    Normal { " and did not apply to leave taken prior to the 2019-2020 academic year." }
                }
            }

            P {
                InlineText {
                    Normal { "Residents could take an additional 2 weeks off during the first 3 years of residency, for a total of " }
                    Bold { "142 weeks" }
                    Normal { " required, and an additional 2 weeks off during the last 2 years of residency, for a total of " }
                    Bold { "94 weeks" }
                    Normal { " required." }
                    Normal(modifier = Modifier.fontStyle(FontStyle.Italic)) { " Note: This was an ABS policy only and should not be confused with family leave as permitted by the Family and Medical Leave Act (FMLA). " }
                    Bold(modifier = Modifier.fontStyle(FontStyle.Italic)) { " No approval was needed for this option if taken as outlined." }
                }
            }
            P {
                SpanText("Updated: June 2022")
            }
        }
    }
}

@Composable
fun SurgerySection(header: String, body: @Composable () -> Unit) {
    P {
        SpanText(modifier = Modifier.fontSize(FontSize.Larger).fontWeight(FontWeight.Bold), text = header)
        Br {}
        body()
    }
}