package io.github.rgreb.resleave.pages.programs

import androidx.compose.runtime.Composable
import com.varabyte.kobweb.compose.css.FontSize
import com.varabyte.kobweb.compose.css.FontWeight
import com.varabyte.kobweb.compose.css.TextDecorationLine
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.fontSize
import com.varabyte.kobweb.compose.ui.modifiers.fontWeight
import com.varabyte.kobweb.compose.ui.modifiers.textDecorationLine
import com.varabyte.kobweb.compose.ui.toAttrs
import com.varabyte.kobweb.core.Page
import com.varabyte.kobweb.silk.components.text.SpanText
import io.github.rgreb.resleave.components.layouts.NavLayout
import io.github.rgreb.resleave.components.widgets.CenteredHeaderWithOptionalPDF
import io.github.rgreb.resleave.components.widgets.LiPSpanText
import io.github.rgreb.resleave.components.widgets.fillWidthAndCenterText
import io.github.rgreb.resleave.components.widgets.underlined
import io.github.rgreb.resleave.pages.Department
import org.jetbrains.compose.web.dom.*

@Page("family_and_community_medicine")
@Composable
fun FamilyMedicine() {
    NavLayout {
        Column() {
            CenteredHeaderWithOptionalPDF(
                Department.FAMILY_AND_COMMUNITY_MEDICINE.value,
                "https://www.theabfm.org/sites/default/files/PDF/ABFM%20Family%20Leave%20Policy-5-21-2020.pdf"
            )
            H3(attrs = Modifier.fillWidthAndCenterText().toAttrs()) {
                SpanText(
                    "ABFM Family Leave Policy and Time Away from Training Guidelines for Board-Eligibility"
                )
                Br {}
                SpanText("May 2020", modifier = Modifier.fontSize(FontSize.Medium))
            }
            H4(attrs = Modifier.fillWidthAndCenterText().underlined().toAttrs()) {
                SpanText("Residency Training Requirements for Board Certification Eligibility")
            }
            P {
                SpanText(
                    "Candidates for certification are required to complete 36 months of graduate medical education in an ACGME " +
                            "accredited Family Medicine residency program. In some situations, the training may be extended for " +
                            "additional time to meet the minimum requirements. All residents must have core clinical training that includes " +
                            "the breadth and depth of Family Medicine. These include, but are not limited to:"
                )
                Ol {
                    LiPSpanText(
                        "Residents are required to spend their PGY-2 and PGY-3 training in the same residency programs " +
                                "teaching practice, in order to provide sustained continuity of care to their patients."
                    )
                    LiPSpanText(
                        "Each year of residency must include a minimum of 40 weeks of continuity clinic experience " +
                                "(exceptions may apply if the residency program has received a waiver of this requirement in " +
                                "connection with pilot projects assessing intentional variation in training requirements) "
                    )
                    LiPSpanText(
                        "Residents are required to complete a minimum of 1650 in-person patient encounters in the " +
                                "continuity practice site to be eligible for ABFM certification"
                    )

                }
                P {
                    SpanText(
                        "At the end of training, the Program Director is expected to sign, on behalf of the program, that the resident has " +
                                "met all requirements for board eligibility and is ready for autonomous practice."
                    )
                }
            }
            H4(attrs = Modifier.underlined().fillWidthAndCenterText().toAttrs()) {
                SpanText("New Policy Parameters")
            }
            P {
                SpanText(
                    "Family Leave provided under this new policy is intended to be provided in the same circumstances specified " +
                            "in the federal Family and Medical Family Leave Act (FMLA), including:"
                )
                Ul {
                    LiPSpanText(
                        "The birth and care of a newborn, adopted, or foster child, including both birth- and non-birth " +
                                "parents of a newborn."
                    )
                    LiPSpanText("The care of a family member* with a serious health condition, including end of life care ")
                    LiPSpanText(" A residents own serious health condition requiring prolonged evaluation and treatment ")
                }
                SpanText(
                    "*Decisions about what constitutes family member and what constitutes serious health condition is best left " +
                            "to the Program Director and their institutional policies. ABFM intends to leave those decisions at the local " +
                            "level where they are best able to be individually made.",
                    modifier = Modifier.fontSize(FontSize.Small)
                )
            }
            P {
                SpanText("This policy does ")
                SpanText("not ", modifier = Modifier.fontWeight(FontWeight.Bold))
                SpanText(
                    """apply to other types of personal leave and/or interruptions from a residency (e.g., prolonged vacation/travel, unaccredited research experience, unaccredited clinical experience, military or government assignment outside the scope of the specialty, etc.). This policy likewise does not apply to periods of time for which a resident does not qualify for credit by reason of residents failure to meet academic, clinical, or professional performance standards. """
                )
            }
            P {
                SpanText(
                    "ABFM does not require approval of a residents Family Leave if it is taken as outlined, and as long as the resident " +
                            "is on schedule to meet other training requirements. "
                )
                SpanText(
                    "ABFM still requests that residencies report in " +
                            "RTM any Family Leave or other LOA, even when extension of training is not required, to allow for data " +
                            "tracking that supports ongoing evaluation of this policy change. ",
                    modifier = Modifier.fontWeight(FontWeight.Bold)
                )
            }

            H4(attrs = Modifier.underlined().fillWidthAndCenterText().toAttrs()) {
                SpanText("Time Allowed for Family Leave of Absence")
            }
            P {
                SpanText("Family Leave", modifier = Modifier.fontWeight(FontWeight.Bold))
                SpanText(
                    " Within a Training Year",
                    modifier = Modifier.fontWeight(FontWeight.Bold).textDecorationLine(TextDecorationLine.Underline)
                )
                SpanText(
                    ": ABFM will allow up to (12) weeks away from the program in a given " +
                            "academic year without requiring an extension of training, as long as the Program Director and CCC agree that " +
                            "the resident is ready for advancement, and ultimately for autonomous practice. This includes up to (8) weeks " +
                            "total attributable to Family Leave, with any remaining time up to (4) weeks for Other Leave as allowed by the " +
                            "program. "
                )
            }
            P {
                SpanText(
                    "There is no longer a requirement to show 12 months in each PGY-year for the resident to be board-eligible; " +
                            ", by virtue of the allowable time, a resident must have at least 40 weeks of formal training in the " +
                            "year in which they take Family Leave. This policy also supplants the previous 30 day limit per year for " +
                            "resident time away from the program. "
                )
            }
            P {
                SpanText("Total Time Away", modifier = Modifier.fontWeight(FontWeight.Bold))
                SpanText(
                    " Across Training",
                    modifier = Modifier.fontWeight(FontWeight.Bold).textDecorationLine(TextDecorationLine.Underline)
                )
                SpanText(
                    ": A resident may take up to a maximum of 20 weeks of leave over the three " +
                            "years of residency without requiring an extension of training. Generally speaking, 9-12 weeks (3-4 weeks per " +
                            "year) of this leave will be from institutional allowances for time off for all residents; programs will continue " +
                            "to follow their own institutional or programmatic leave policies for this."
                )
            }

            P {
                SpanText(
                    "If a residents leave exceeds either 12 weeks away from the program in a given year, and/or a maximum of  " +
                            "20 weeks total, (e.g. second pregnancy, extended or recurrent personal or family leave) extension of the  " +
                            "residents training will be necessary to cover the duration of time that the individual was away from the  " +
                            "program in excess of 20 weeks. "
                )
            }
            P {
                SpanText(
                    "Residency Directors must make appropriate curricular adjustments and notify ABFM of requested extensions  " +
                            "through the RTM system, for approval by ABFM. Reports must include an explanation for the absence from  " +
                            "training, the number of total days missed, and a plan for resuming training as basis for calculating a new  " +
                            "graduation date. Residents must still achieve 1650 continuity visits by the end of residency."
                )
            }
            H4(attrs = Modifier.underlined().fillWidthAndCenterText().toAttrs()) {
                SpanText("Additional Considerations:")
            }
            Ul {
                LiPSpanText(
                    "ABFM will allow Family Leave to cross over two academic years. In this circumstance, the Program  " +
                            "Director and sponsoring institution will be the ones to decide when the resident is advanced from one  " +
                            "PGY-year to the next."
                )
                LiPSpanText(
                    "Other Leave time may be utilized as part of approved Family Leave, or in addition to approved Family  " +
                            "Leave. ABFM encourages programs to preserve a minimum of one week of Other Leave in any year in  " +
                            "which a resident takes Family Leave. Consideration should be given to the importance of preserving  " +
                            "some time away for resident well-being outside of a period of Family Leave."
                )
                LiPSpanText(
                    "Residents are expected to take allotted time away from the program for Other Leave according to local  " +
                            "institutional policies. Foregoing this time by banking it in order to shorten the required 36 months of  " +
                            "residency or to retroactively make up for time lost due to sickness or other absence is not permitted. "
                )
                LiPSpanText(
                    "Time missed for educational conferences does not count toward the time away from training under the  " +
                            "Family Leave time allowed in this policy.  "
                )
            }
            H4(attrs = Modifier.underlined().fillWidthAndCenterText().toAttrs()) {
                SpanText(
                    "Certification Timeline in Instances of Extension of Residency Training",
                    modifier = Modifier.underlined()
                )
            }
            P {
                SpanText(
                    "When a residents training completion dates change, ABFM will provide opportunities to take their initial  " +
                            "certification exam within the year, as described below:"
                )
                Ol {
                    LiPSpanText(
                        "If they are anticipated to complete training between July 1st and October 31  " +
                                "st, they may apply for and " +
                                "take the Certification Examination in April of their PGY-3 year, with permission from the program  " +
                                "director through the RTM system."
                    )
                    LiPSpanText(
                        "If they are anticipated to complete their residency between November 1st and December 31st, they  " +
                                "will be eligible to take the Certification Examination in November of their graduating year. Residents  " +
                                "completing training between January 1 and April 30 of the following year may also apply for the  " +
                                "November exam with permission from the program director through the RTM system. "
                    )
                }
            }
        }
    }
}