package io.github.rgreb.resleave.pages.programs

import androidx.compose.runtime.Composable
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.toAttrs
import com.varabyte.kobweb.core.Page
import com.varabyte.kobweb.silk.components.navigation.Link
import com.varabyte.kobweb.silk.components.text.SpanText
import io.github.rgreb.resleave.components.layouts.NavLayout
import io.github.rgreb.resleave.components.widgets.CenteredHeaderWithOptionalPDF
import io.github.rgreb.resleave.components.widgets.fillWidthAndCenterText
import io.github.rgreb.resleave.pages.Department
import org.jetbrains.compose.web.dom.H3
import org.jetbrains.compose.web.dom.P

@Page("internal_medicine")
@Composable
fun InternalMedicine() {
    NavLayout {
        Column() {
            CenteredHeaderWithOptionalPDF(
                Department.INTERNAL_MEDICINE.value,
                "https://www.abim.org/certification/policies/special-training-policies"
            )
            H3(attrs = Modifier.fillWidthAndCenterText().toAttrs()) {
                SpanText("Leave of Absence and Vacation")
            }
            P {
                SpanText(
                    "Up to 5 weeks (35 days) per academic year are cumulatively permitted over the course of the training program for time away from training, which includes vacation, illness, parental or family leave, or pregnancy-related disabilities. For example, a resident could take 105 days of leave during a three-year internal medicine residency without needing to extend training. Training must be extended to make up any absences exceeding 5 weeks (35 days) per year of training unless the Deficits in Required Training Time policy is used. Vacation leave is essential and should not be forfeited or postponed in any year of training and cannot be used to reduce the total required training period. ABIM does not establish how much time per year should be used for vacation and recognizes that leave policies vary from institution to institution. Program Directors may apply their local requirements within these guidelines to ensure trainees have completed the requisite period of training with adequate vacation over the total training duration."
                )
            }
            P {
                SpanText("ABIM considers activities such as attendance at training-related seminars, courses, interviews for subsequent training positions or jobs, etc., as bona fide educational experiences or duties essential for the continuity of education in internal medicine and its subspecialties. These activities need not be counted as part of the allocation for leave time in the academic year for purposes of tracking training time for ABIM. Similarly, ABIM does not require that this time be counted among the educational experiences of the training program; rather, the program director has the discretion to apply this policy to ensure the balance of time needed to assure competency in the discipline is achieved at the end of training.")
            }
            P {
                SpanText("This policy, along with ABIM's ")
                Link(
                    "https://www.abim.org/certification/policies/special-training-policies/#deficits",
                    "Deficits in Required Training Time"
                )
                SpanText(" policy provides for time away from training in excess of the minimum requirements of the ")
                Link("https://www.abms.org/policies/parental-leave/", "ABMS Leave Policy,")
                SpanText(" as revised in July 2021. Under the ABMS policy, Member Boards with requirements that allow for more than six weeks of time away from training for any purpose including parental, caregiver and medical leave are in compliance with the above policy. (ABMS Leave Policy, Appendix,  3.) ABIMs policies allow for more than six weeks of time away from training for any purpose.")
            }
        }
        H3(attrs = Modifier.fillWidthAndCenterText().toAttrs()) {
            SpanText("Interrupted Full-Time Training")
        }
        P {
            SpanText(
                text = "ABIM approval must be obtained before initiating an interrupted training plan. Interrupted full-time training is acceptable, provided that no period of full-time training is less than one month. In any 12-month period, at least six months should be spent in training. During training periods, patient care responsibilities should be maintained in a continuity clinic consistent with ACGME program requirements for the discipline. Part-time training, whether or not continuous, is not acceptable."
            )
        }
    }
}