package io.github.rgreb.resleave.components.widgets

import androidx.compose.runtime.Composable
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.silk.components.text.SpanText
import org.jetbrains.compose.web.dom.ElementScope
import org.jetbrains.compose.web.dom.Li
import org.jetbrains.compose.web.dom.P
import org.jetbrains.compose.web.dom.Ul
import org.w3c.dom.HTMLOListElement
import org.w3c.dom.HTMLUListElement

@Composable
fun ElementScope<HTMLOListElement>.SimpleList(list: List<String>) {
    Ul {
        list.forEach {
            Li {
                P {
                    SpanText(it)
                }
            }
        }
    }
}

@Composable
fun ElementScope<HTMLUListElement>.SimpleList(list: List<String>) {
    Ul {
        list.forEach {
            Li {
                P {
                    SpanText(it)
                }
            }
        }
    }
}

@Composable
fun ElementScope<HTMLOListElement>.LiPSpanText(text: String, modifier: Modifier = Modifier) {
    Li {
        P {
            SpanText(text, modifier)
        }
    }
}

@Composable
fun ElementScope<HTMLOListElement>.LiPSpan(modifier: Modifier = Modifier, content: @Composable () -> Unit) {
    Li {
        P {
            content()
        }
    }
}

@Composable
fun ElementScope<HTMLUListElement>.LiPSpanText(text: String, modifier: Modifier = Modifier) {
    Li {
        P {
            SpanText(text, modifier)
        }
    }
}

@Composable
fun ElementScope<HTMLUListElement>.LiPSpan(modifier: Modifier = Modifier, content: @Composable () -> Unit) {
    Li {
        P {
            content()
        }
    }
}
