import androidx.compose.runtime.CompositionLocalProvider
import com.varabyte.kobweb.core.AppGlobalsLocal
import com.varabyte.kobweb.navigation.RoutePrefix
import com.varabyte.kobweb.navigation.Router
import com.varabyte.kobweb.silk.defer.renderWithDeferred
import kotlin.Unit
import kotlinx.browser.document
import kotlinx.browser.window
import org.jetbrains.compose.web.renderComposable

public fun main(): Unit {
    RoutePrefix.set("")
    val router = Router()
    com.varabyte.kobweb.core.init.initKobweb(router) { ctx ->
        ctx.router.register("/") { io.github.rgreb.resleave.pages.Home() }
        ctx.router.register("/programs/anesthesiology") {
                io.github.rgreb.resleave.pages.programs.Anesthesiology() }
        ctx.router.register("/programs/clinical_pathology") {
                io.github.rgreb.resleave.pages.programs.ClinicalPathology() }
        ctx.router.register("/programs/dermatology") {
                io.github.rgreb.resleave.pages.programs.Dermatology() }
        ctx.router.register("/programs/emergency_medicine") {
                io.github.rgreb.resleave.pages.programs.EmergencyMedicine() }
        ctx.router.register("/programs/family_and_community_medicine") {
                io.github.rgreb.resleave.pages.programs.FamilyMedicine() }
        ctx.router.register("/programs/internal_medicine") {
                io.github.rgreb.resleave.pages.programs.InternalMedicine() }
        ctx.router.register("/programs/neurological_surgery") {
                io.github.rgreb.resleave.pages.programs.NeurologicalSurgery() }
        ctx.router.register("/programs/neurology") {
                io.github.rgreb.resleave.pages.programs.Neurology() }
        ctx.router.register("/programs/obstetrics_and_gynecology") {
                io.github.rgreb.resleave.pages.programs.ObstetricsAndGynecology() }
        ctx.router.register("/programs/ophthalmology") {
                io.github.rgreb.resleave.pages.programs.Ophthalmology() }
        ctx.router.register("/programs/orthopaedic_surgery") {
                io.github.rgreb.resleave.pages.programs.OrthopaedicSurgery() }
        ctx.router.register("/programs/otolaryngology") {
                io.github.rgreb.resleave.pages.programs.Otolaryngology() }
        ctx.router.register("/programs/pediatrics") {
                io.github.rgreb.resleave.pages.programs.Pediatrics() }
        ctx.router.register("/programs/plastic_surgery") {
                io.github.rgreb.resleave.pages.programs.PlasticSurgery() }
        ctx.router.register("/programs/pmr") { io.github.rgreb.resleave.pages.programs.PMR() }
        ctx.router.register("/programs/psychiatry") {
                io.github.rgreb.resleave.pages.programs.Psychiatry() }
        ctx.router.register("/programs/radiation_oncology") {
                io.github.rgreb.resleave.pages.programs.RadiationOncology() }
        ctx.router.register("/programs/radiology") {
                io.github.rgreb.resleave.pages.programs.Radiology() }
        ctx.router.register("/programs/surgery") { io.github.rgreb.resleave.pages.programs.Surgery()
                }
        ctx.router.register("/programs/thoracic_surgery") {
                io.github.rgreb.resleave.pages.programs.ThoracicSurgery() }
        ctx.router.register("/programs/urology") { io.github.rgreb.resleave.pages.programs.Urology()
                }

    }
    router.addRouteInterceptor {
        path = path.removeSuffix(".html").removeSuffix(".htm")
    }

    com.varabyte.kobweb.silk.init.initSilkHook = { ctx ->
        ctx.theme.registerComponentStyle(io.github.rgreb.resleave.components.sections.NavMenuButtonStyle)
        ctx.theme.registerComponentStyle(io.github.rgreb.resleave.components.sections.NavMenuStyle)
        ctx.theme.registerComponentStyle(io.github.rgreb.resleave.components.sections.NavMenuItemStyle)
        ctx.theme.registerComponentStyle(io.github.rgreb.resleave.components.sections.InlineLink)
        ctx.theme.registerComponentStyle(io.github.rgreb.resleave.components.widgets.PdfLinkStyle)
        ctx.theme.registerComponentStyle(io.github.rgreb.resleave.components.widgets.FileAnIssueStyle)
        io.github.rgreb.resleave.pages.updateTheme(ctx)
    }

    router.navigateTo(window.location.href.removePrefix(window.location.origin))

    // For SEO, we may bake the contents of a page in at build time. However, we will overwrite them
    // the first time we render this page with their composable, dynamic versions. Think of this as
    // poor man's hydration :)
    // See also: https://en.wikipedia.org/wiki/Hydration_(web_development)
    val root = document.getElementById("root")!!
    while (root.firstChild != null) {
        root.removeChild(root.firstChild!!)
    }

    renderComposable(rootElementId = "root") {
        CompositionLocalProvider(
            AppGlobalsLocal provides mapOf("title" to "Resident Leave at UC Davis")
        ) { io.github.rgreb.resleave.pages.Main {
              router.renderActivePage { renderWithDeferred { it() } }
        } }
    }
}
