package io.github.rgreb.resleave.pages.programs

import androidx.compose.runtime.Composable
import com.varabyte.kobweb.compose.css.ListStyleType
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.listStyle
import com.varabyte.kobweb.compose.ui.toAttrs
import com.varabyte.kobweb.core.Page
import com.varabyte.kobweb.silk.components.text.SpanText
import io.github.rgreb.resleave.components.layouts.NavLayout
import io.github.rgreb.resleave.components.widgets.CenteredHeaderWithOptionalPDF
import io.github.rgreb.resleave.components.widgets.LiPSpan
import io.github.rgreb.resleave.components.widgets.LiPSpanText
import org.jetbrains.compose.web.dom.*

@Page("clinical_pathology")
@Composable
fun ClinicalPathology() {
    NavLayout {
        Column() {
            CenteredHeaderWithOptionalPDF(
                "Clinical Pathology",
                "https://www.abpath.org/images/booklets/ABP_BOI.pdf"
            )
            H3 {
                SpanText("Policy on Vacation, Sick, Parental, Caregiver, and Medical Leave During Training")
            }
            P {
                SpanText(
                    "Each sponsoring institution must have a policy for vacation and other leaves of" +
                            "absence (ACGME Common PR). Regardless of institutional policies, the ABPath" +
                            "policy is:"
                )

                Ol(attrs = Modifier.listStyle(ListStyleType.LowerAlpha).toAttrs()) {
                    Li {
                        P {
                            SpanText(
                                "One (1) year of training to meet ABPath certification requirements must be 52 " +
                                        "weeks in duration, and the applicant must document an average of 48 weeks per " +
                                        "year of full-time pathology training over the course of the training program ."
                            )
                        }
                    }
                    Li {
                        P {
                            SpanText(
                                "Any training less than 48 weeks must be made up with the following exception in " +
                                        "III.A.3.c below (this exception does not apply to fellowships):"
                            )
                        }

                        Li {
                            H4 {
                                SpanText("Parental, Caregiver and Extended Medical Leave")
                            }
                            P {
                                SpanText("This policy does not apply to fellowships.")
                                Br {}
                                SpanText(
                                    "The ABPath will allow up to 6 weeks of time away from training for purposes of " +
                                            "parental, caregiver, and extended medical leave without needing to extend the " +
                                            "duration of training."
                                )
                            }
                            Ol(attrs = Modifier.listStyle(ListStyleType.LowerRoman).toAttrs()) {

                                LiPSpanText(
                                    "Parental leave includes all new parents, i.e. birthing and non-birthing parents, " +
                                            "adoptive/foster parents, and surrogates."
                                )


                                LiPSpanText(
                                    "Caregiver leave includes care of an immediate family member (child, spouse " +
                                            "or parent) with a serious health condition."
                                )

                                LiPSpanText(
                                    "This time away is in addition to vacation time, which may be accrued or " +
                                            "averaged over the course of a training program."
                                )
                                LiPSpanText(
                                    "For candidates who use time away from training for parental, caregiver, " +
                                            "and extended medical leave, the ABPath will require the candidates program " +
                                            "director and clinical competency committee to document and to attest " +
                                            "that competency has been achieved without the need for an extension of " +
                                            "training."
                                )

                                LiPSpanText(
                                    "Training is expected to be extended when the clinical competency " +
                                            "committee has determined that competency has not been achieved."
                                )
                            }
                        }
                        LiPSpanText(
                            "The ABPath does not allow part-time training for primary certification. A one-year " +
                                    "(12 month) subspecialty fellowship may be completed part-time overtwoyears(24 " +
                                    "months)."
                        )

                        LiPSpan {
                            SpanText(
                                "With the approval of the Program Director, a resident is allowed up to 12 months " +
                                        "away from training for any reason. The applicant is still required to complete on " +
                                        "average 48 weeks per year of full-time training over the course of the training " +
                                        "program. "
                            )
                            Br()
                            SpanText(
                                "The ABPath does not allow credit for student fellowships in pathology during " +
                                        "medical school because this is not ACGME accredited training. The ABPath does not " +
                                        "give advanced credit for any training outside of the US or Canada or for training " +
                                        "in non-accredited programs for either primary or subspecialty certification."
                            )
                        }
                    }
                }
            }
        }
    }
}
