package io.github.rgreb.resleave.pages.programs

import androidx.compose.runtime.Composable
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.toAttrs
import com.varabyte.kobweb.core.Page
import com.varabyte.kobweb.silk.components.text.SpanText
import io.github.rgreb.resleave.components.layouts.NavLayout
import io.github.rgreb.resleave.components.widgets.CenteredHeaderWithOptionalPDF
import io.github.rgreb.resleave.components.widgets.LiPSpanText
import io.github.rgreb.resleave.components.widgets.fillWidthAndCenterText
import io.github.rgreb.resleave.pages.Department
import org.jetbrains.compose.web.dom.H3
import org.jetbrains.compose.web.dom.H4
import org.jetbrains.compose.web.dom.Ol
import org.jetbrains.compose.web.dom.P

@Page("neurological_surgery")
@Composable
fun NeurologicalSurgery() {
    NavLayout {
        Column() {
            CenteredHeaderWithOptionalPDF(
                Department.NEUROLOGICAL_SURGERY.value,
                "https://abns.org/family-and-medical-leave/"
            )
            H3(attrs = Modifier.fillWidthAndCenterText().toAttrs()) {
                SpanText("Statement On Family And Medical Leave")
            }
            H4 {
                SpanText("Residency")
            }
            P {
                SpanText("For residents who desire to take time away from training for legitimate family or medical reasons (e.g., the birth or adoption of a child; extended illness), the program director should follow the human resources policies of their institution and applicable law when determining whether to approve such leave requests, in whole or in part and whether any time away from training needs to be made up at the backend of the residents training. It is the program directors responsibility to ensure that residents who receive less than 84 months of training successfully complete the program, receive the full range of neurosurgical training and can practice as safe neurosurgeons following their residency.")
            }
            P {
                SpanText("If any leave results in a resident receiving less than 84 total months of training in residency, the following applies:")
                Ol {
                    LiPSpanText("Under no circumstances may a resident receive less than 54 months of core training (I.e., any reduction from the standard 84 months of training must come from the residents 30 months of elective time).")
                    LiPSpanText("In connection with the residents future application for certification, the residents program director must submit a letter to the ABNS indicating: (a) that the program director approved any leave that resulted in less than 84 months of training; and (b) that the program director determined that the resident at issue received full training and was able to successfully complete residency in less than 84 months.")
                }
            }
            H4 {
                SpanText("Post-Residency")
            }
            listOf(
                "The ABNS is aware that many neurological surgeons take time off or work reduced schedules at some juncture during their first few years of practice for legitimate family reasons, such as in connection with the birth or adoption of a child, to care for an immediate family member with a serious health condition, or when the physician themselves is unable to work due to a serious medical condition.",
                "Taking time off or working a reduced schedule may make it difficult for certification candidates to meet one or both of two ABNS requirements: (1.) The requirement that candidates collect and submit 125 consecutive cases over a period of no more than 18 months; and (2.) The requirement that candidates submit their completed applications no later than three years following the completion of residency training.",
                "ABNS rules permit the ABNS directors to extend these time periods at their discretion, on a case-by-case basis for good cause. Requests for extensions must be made in writing and submitted to the ABNS administrative office. As a policy matter, the ABNS directors generally grant extension requests that are related to absence of practice (or reduced work schedules) for legitimate family leave reasons.",
                "However, the ABNS is also wary of potential policy abuses. Consequently, any granted extensions will be no more than appropriate considering the circumstances in each case. In addition, the ABNS may request documentation of a candidates absence from practice or reduced time schedule for family leave reasons."
            )
                .forEach {
                    P { SpanText(it) }
                }
        }
    }
}