const val COLOR_MODE_KEY = "lakjfaksdj"

@InitSilk
fun updateTheme(ctx: InitSilkContext) {
    ctx.config.initialColorMode = localStorage.getItem(COLOR_MODE_KEY)?.let { ColorMode.valueOf(it) } ?: ColorMode.DARK
    ctx.stylesheet.registerBaseStyle("body") {
        Modifier.fontFamily(
            "JetBrains Sans",
            "Inter",
            "system-ui",
            "-apple-system",
            "BlinkMacSystemFont",
            "Segoe UI",
            "Roboto",
            "Oxygen",
            "Ubuntu",
            "Cantarell",
            "Droid Sans",
            "Helvetica Neue",
            "Arial",
            "sans-serif"
        )
    }
}

@App
@Composable
fun Main(content: @Composable () -> Unit) {
    SilkApp {
        Surface(
            modifier = SmoothColorStyle.toModifier().backgroundColor(
                colors(
                    ifDark = { com.varabyte.kobweb.compose.ui.graphics.Color.rgb(25, 25, 28) },
                    ifLight = { org.jetbrains.compose.web.css.Color.ghostwhite }
                )
            )
                .padding(24.px)
                .minHeight(100.vh)
        ) {
            Row(modifier = Modifier.fillMaxSize()) {
                content()
            }
        }
    }
}

@Page
@Composable
fun Home() {
    NavLayout {
        Column() {
            CenteredHeaderWithOptionalPDF(
                "American Board of Medical Specialties Policy on Parental, Caregiver and Medical Leave During Training",
                "https://www.abms.org/wp-content/uploads/2020/11/parental-caregiver-and-medical-leave-during-training-policy.pdf"
            )

            H3(attrs = Modifier.fillMaxWidth().textAlign(TextAlign.Center).toAttrs()) {
                SpanText("Effective July 1, 2021")
            }

            SpanText(intro)
            P {
                SpanText("Policy", modifier = Modifier.fontWeight(FontWeight.Bold).fontSize(FontSize.Large))

                Ol {
                    abmsList.subList(0, 7).forEach {
                        P {
                            Li(attrs = Modifier.fontSize(FontSize.Large).toAttrs()) {
                                SpanText(it)

                            }
                        }
                    }
                }
            }
            Small(attrs = Modifier.flexWrap(FlexWrap.Wrap).toAttrs()) {
                Text(disclaimer)
            }
        }
    }
}

