package io.github.rgreb.resleave.components.widgets

import androidx.compose.runtime.Composable
import com.varabyte.kobweb.compose.css.TextAlign
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.borderBottom
import com.varabyte.kobweb.compose.ui.modifiers.fillMaxWidth
import com.varabyte.kobweb.compose.ui.modifiers.textAlign
import com.varabyte.kobweb.compose.ui.toAttrs
import com.varabyte.kobweb.silk.components.style.toAttrs
import com.varabyte.kobweb.silk.components.text.SpanText
import org.jetbrains.compose.web.css.LineStyle
import org.jetbrains.compose.web.css.px
import org.jetbrains.compose.web.dom.A
import org.jetbrains.compose.web.dom.H2

@Composable
fun CenteredHeaderWithOptionalPDF(headerText: String, url: String?) {
    Column(
        modifier = Modifier.fillMaxWidth()
            .textAlign(TextAlign.Center)
            .borderBottom(
                width = 1.px, LineStyle.Solid
            )
    ) {
        H2(attrs = Modifier.fillMaxWidth().toAttrs()) {
            SpanText(headerText)
            A(href = url, attrs = PdfLinkStyle.toAttrs()) {
                SpanText(" [goto]")
            }
        }
    }
}